@import url(../../../node_modules/angular-material/angular-material.min.css);

.bids_container {
  margin-bottom: 28px;
}

.bids_title {
  width: 100%;
  height: 33px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.bids_title__text {
  height: 33px;
  opacity: 0.98;
  font-size: 24px;
  font-weight: bold;
  color: #000000;
}

.bids_title__link {
  width: 47px;
  height: 20px;
  opacity: 0.98;
  font-size: 15px;
  font-weight: 600;
  line-height: 14px;
  color: #007fed;
}

.bids_row {
  display: flex;
  flex-wrap: wrap;
  margin: 16px -16px 0 -6px;
}

.bids_card_wrapper {
  width: 33%;
  box-sizing: border-box;
  padding: 0 6px;
  min-width: 256px;
  margin-bottom: 12px;
}

.bids_card {
  border-radius: 4px;
  padding: 12px 16px;
}

.bids_card:first-child {
  margin-left: 0;
}

.bids_card:last-child {
  margin-right: 0;
}

.bids_card__green {
  background-color: #e7f7e4;
}

.bids_card__red {
  background-color: #fff2ec;
}

.bids_card__blue {
  background-color: #edf8fd;
}

.bids_card__grey {
  background-color: #edebf6;
}

.bids_card__title {
  display: flex;
}

.bids_card__title_img {
  margin-top: 4px;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.bids_card__title_img img {
  width: 100%;
}

.bids_card__title_text {
  padding-left: 10px;
  display: flex;
  flex-direction: column;
}

.bids_card__title_text span:first-child {
  font-size: 15px;
  font-weight: 600;
}

.bids_card__title_text span:last-child {
  font-size: 12px;
}

.bids_card__price {
  display: flex;
  align-items: center;
  margin-top: 8px;
  height: 20px;
}

.bids_card__price_dot {
  width: 5px;
  height: 5px;
  margin: 8px;
  border-radius: 2px;
  background-color: #cdcdcd;
}

.bids_card__price span {
  font-size: 15px;
  color: #949494;
}

.bids_card__options {
  margin-top: 8px;
  display: flex;
  justify-content: space-between;
}

.bids_card__options_column {
  display: flex;
  flex-direction: column;
  padding: 0 2px;
}

.bids_card__options_column:first-child {
  padding-left: 0;
}

.bids_card__options_column:last-child {
  padding-right: 0;
}

.bids_card__options_column span {
  white-space: nowrap;
}

.bids_card__options_column span:first-child {
  line-height: 17px;
  font-size: 12px;
  color: #949494;
}

.bids_card__options_column span:last-child {
  font-size: 15px;
  line-height: 20px;
}

.bids-common {
  display: flex;
  flex-direction: column;
}

.bids-common__title {
  width: 100%;
  font-size: 12px;
  line-height: 17px;
  color: #949494;
  margin-bottom: 4px;
  display: flex;
}

.bids-common__title span {
  display: flex;
  width: 25%;
  padding-left: 10px;
  box-sizing: border-box;
}

.bids-common__title span:first-child {
  padding-left: 0;
}

.bids-common__title span:last-child {
  flex-grow: 1;
}

.bids-common__items {
  display: flex;
  width: 100%;
}

.bids-common__items-yellow span,
.bids-common__items-yellow input {
  background: #fff9e8;
}

.bids-common__items-blue span,
.bids-common__items-blue input {
  background: #edf8fd;
}

.bids-common__items span,
.bids-common__items input {
  height: 40px;
  box-sizing: border-box;
  font-size: 15px;
  line-height: 40px;
  border: none;
  border-left: 1px solid white;
  margin: 0;
  display: flex;
  width: 25%;
  padding: 0 10px;
}

.bids-common__items span:first-child,
.bids-common__items input:first-child {
  border-radius: 4px 0 0 4px;
}

.bids-common__items span:last-child,
.bids-common__items input:last-child {
  flex-grow: 1;
  border-radius: 0 4px 4px 0;
}

.bids-common__items span:nth-child(3),
.bids-common__items input:nth-child(3) {
  flex-grow: 1;
  border-radius: 0 4px 4px 0;
}

.sale {
