.sale_container {
  margin-bottom: 28px;
}

.sale_title {
  width: 100%;
  height: 33px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 17px;
}

.sale_title div:first-child {
  text-transform: capitalize;
  margin-right: 7px;
}

.sale_title > div {
  display: flex;
}

.sale_title__text {
  opacity: 0.98;
  font-size: 24px;
  font-weight: bold;
  color: #000000;
  margin-right: 20px;
}

.sale_title__text_disabled {
  color: rgba(0, 0, 0, 0.2);
}

.sale_title__btn {
  width: 127px;
  line-height: 32px;
  border-radius: 6px;
  background-color: #007fed;
  color: #ffffff;
  text-align: center;
  cursor: pointer;
}

.sale_title__link {
  width: 48px;
  opacity: 0.98;
  font-size: 15px;
  font-weight: 600;
  line-height: 14px;
  color: #007fed;
  align-self: flex-end;
}

.sale_row {
  display: flex;
  flex-wrap: wrap;
  margin: 16px -16px 0 -6px;
}

.sale_card_wrapper {
  width: 33%;
  min-width: 256px;
  box-sizing: border-box;
  padding: 0 6px;
  margin-bottom: 12px;
}

.sale_card {
  border-radius: 4px;
  padding: 12px 16px;
}

.sale_card__yellow {
  background-color: #fff9e8;
}

.sale_card__grey {
  background-color: #f6f6f6;
}

.sale_card__title {
  font-size: 15px;
  font-weight: 600;
}

.sale_card__price {
  font-size: 12px;
  font-weight: 400;
}

.sale_card__options {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0 -2px;
}

.sale_card__options_column {
  display: flex;
  flex-direction: column;
  margin-top: 8px;
  padding: 0 2px;
}

.sale_card__options_column span {
  white-space: nowrap;
}

.sale_card__options_column span:first-child {
  font-size: 12px;
  color: #949494;
  line-height: 17px;
}

.sale_card__options_column span:last-child {
  font-size: 15px;
  line-height: 20px;
}

.sale_table {
  width: 100%;
  border-spacing: 0;
}

.sale_table_head {
  width: 100%;
}

.sale_table_head_td {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: left;
  height: 33px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.sale_table_head_td:last-child {
  text-align: right;
}

.sale_table_body__yellow tr td {
  background-color: #fff9e8;
}

.sale_table_body__grey tr td {
  background-color: #f6f6f6;
}

.sale_table_row {
  height: 77px;
  font-size: 15px;
}

.sale_table_row td {
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.sale_table_row td:last-child {
  text-align: right;
  padding-right: 10px;
}

.sale_table_row__title {
  padding-left: 10px;
}

.sale_table_row__title span:first-child {
  font-weight: 600;
}

.sale_table_row__title span:last-child {
  color: rgba(0, 0, 0, 0.6);
}

.sale_table_row__data {
  color: rgba(0, 0, 0, 0.6);
}

.sale_table_row__data_bold {
  font-weight: 700;
  color: #000;
}

.sale_table_row__data_bold_green {
  color: #00c811;
}

.modal_wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: flex-end;
}

.modal_content_wrapper {
  width: 500px;
  height: 100%;
  background-color: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.02), 0 4px 4px 0 rgba(0, 0, 0, 0.02), 0 8px 8px 0 rgba(0, 0, 0, 0.02), 0 16px 16px 0 rgba(0, 0, 0, 0.02);
  border: solid 1px rgba(0, 0, 0, 0.04);
  box-sizing: border-box;
}

.modal_content {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.modal_content__title {
  width: 100%;
  height: 33px;
  font-size: 24px;
  color: #000000;
  font-weight: 700;
  line-height: 33px;
  text-align: left;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.modal_content__title .close {
  width: 14px;
  height: 14px;
  background: url("/images/close.png") no-repeat center;
  background-size: 100%;
  cursor: pointer;
}

.modal_content__sale {
  width: 100%;
}

.modal_content__sale_title {
  font-size: 15px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.34);
  text-transform: uppercase;
  margin-bottom: 12px;
}

.modal_content__sale_title_select {
  text-transform: capitalize;
}

.modal_content__sale_title_select md-select-menu {
  margin-left: -10px !important;
}

.modal_content__sale_select {
  width: 100%;
  height: 38px;
  background-color: rgba(0, 0, 0, 0.04);
  border-radius: 6px;
  margin-bottom: 16px;
  margin-top: 4px;
}

.modal_content__sale .attributs_wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.modal_content__sale .attributs_wrapper .attributs_item {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 20px;
}

.modal_content__sale .attributs_wrapper .attributs_item span {
  display: block;
  margin-bottom: 4px;
}

.modal_content__sale .attributs_wrapper .attributs_item input {
  border: none;
  outline: none;
  border-bottom: 1px solid #f2f2f2;
  padding-bottom: 20px;
  font-size: 18px;
  color: #000;
}

.modal_content__sale .attributs_wrapper .attributs_item input::placeholder {
  color: #949494;
}

.modal_content__sale .attributs_wrapper .attributs_item input:focus::placeholder {
  color: transparent;
}

.modal_content__sale .attributs_wrapper .attributs_item:first-child {
  width: 45%;
}

.modal_content__sale .attributs_wrapper .attributs_item:last-child {
  width: 55%;
}

.modal_content__sale .attributs_wrapper .attributs_item__border {
  width: 132px !important;
  overflow: hidden;
}

.modal_content__sale .attributs_wrapper .attributs_item__border input,
.modal_content__sale .attributs_wrapper .attributs_item__border div {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.04);
  box-sizing: border-box;
  padding: 8px 0 10px 12px;
  border-radius: 6px;
}

.modal_content__sale .attributs_wrapper .attributs_item__border > div {
  height: 42px;
  color: black;
  font-size: 18px;
}

.modal_foter {
  width: 500px;
  height: 70px;
  border-top: 1px solid #f2f2f2;
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.modal_foter__buttons {
  display: flex;
  height: 38px;
  margin-right: 40px;
}

.modal_foter__buttons .button_blue {
  width: 148px;
  height: 38px;
  line-height: 38px;
  border-radius: 6px;
  background-color: #007fed;
  color: #fff;
  text-align: center;
  font-size: 15px;
  font-weight: 600;
  margin-left: 20px;
  cursor: pointer;
}

.modal_foter__buttons .button_grey {
  width: auto;
  padding: 0 27px;
  background-color: rgba(0, 0, 0, 0.04);
  color: #000000;
}

.modal_foter__buttons .button_text {
  height: 38px;
  line-height: 38px;
  cursor: pointer;
}

.sale-manage__attributes {
  display: flex;
}

.sale-manage__attributes__title {
  font-size: 12px;
  line-height: 17px;
  color: #949494;
  margin-left: 12px;
  margin-bottom: 4px;
}

.sale-manage__attributes__value {
  width: 100%;
  height: 40px;
  box-sizing: border-box;
  background: #fff9e8;
  font-size: 15px;
  line-height: 40px;
  padding: 0 0 0 12px;
  border: none;
  border-left: 1px solid white;
  margin: 0;
}

.sale-manage__attributes__markup {
  display: flex;
  flex-direction: column;
  width: 25%;
}

.sale-manage__attributes__markup .sale-manage__attributes__title {
  margin-left: 0;
}

.sale-manage__attributes__markup .sale-manage__attributes__value {
  font-weight: 600;
  border-left: none;
  border-radius: 4px 0 0 4px;
}

.sale-manage__attributes__share {
  display: flex;
  flex-direction: column;
  width: 25%;
}

.sale-manage__attributes__amount {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.sale-manage__attributes__edit {
  display: flex;
  flex-direction: column;
  width: 40px;
  border-left: 1px solid white;
  justify-content: flex-end;
}

.sale-manage__attributes__btn {
  padding: 0;
  margin: 0;
  width: 40px;
  height: 40px;
  border: none;
  background-color: #fff9e8;
  background-image: url("/images/edit.svg");
  background-repeat: no-repeat;
  background-size: 24px;
  background-position: center;
}

.md-select-menu-container.md-active md-select-menu {
  margin-left: -10px;
}

.event_wrapper {
  display: flex;
  flex-direction: row;
}

.event_left-side {
  padding-top: 6px;
  padding-right: 32px;
  width: 40%;
}

.event_left-side_header {
  display: flex;
  height: 20px;
  margin-bottom: 16px;
}

.event_left-side_header_link {
  display: flex;
  color: #999999;
  font-size: 15px;
  text-decoration: none;
}

.event_left-side_header_link:before {
  content: '';
  display: inline-block;
  width: 12px;
  height: 21px;
  margin-right: 2px;
  background: url(/images/left-arrow.svg) no-repeat center;
  background-size: 12px;
}

.event_left-side_card {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
}

.event_left-side_card_img {
  width: 100%;
  height: auto;
}

.event_left-side_card_img img {
  display: block;
  width: 100%;
  height: auto;
}

.event_left-side_card_title {
  margin: 12px 0;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.17;
}

.event_left-side_card_description {
  display: flex;
  color: #cdcdcd;
  font-size: 15px;
}

.event_left-side_card_description a {
  margin-left: auto;
  color: #007fed;
  font-weight: 600;
}

.event_left-side_list {
  display: flex;
  flex-direction: column;
}

.event_left-side_list_title {
  line-height: 17px;
  font-weight: 600;
  font-size: 15px;
  color: #cdcdcd;
  text-transform: uppercase;
  margin-bottom: 10px;
}

.event_left-side_list_item {
  display: flex;
  cursor: pointer;
  flex-direction: column;
  min-height: 42px;
  padding: 13px 0 15px;
  border-bottom: 1px solid #F2F2F2;
}

.event_left-side_list_item:last-child {
  border: none;
}

.event_left-side_list_item:hover,
.event_left-side_list_item.active {
  background-color: #f6f6f6;
  margin: -2px -16px;
  padding: 15px 16px 17px;
  border-radius: 4px;
  border-bottom-color: #f6f6f6;
}

.event_left-side_list_item_title {
  font-weight: 600;
  font-size: 15px;
  margin-bottom: 4px;
}

.event_left-side_list_item_description {
  display: flex;
  flex-wrap: wrap;
  color: #cdcdcd;
  font-size: 15px;
}

.event_left-side_list_item_description span:last-child {
  margin-left: auto;
}

.event_right-side {
  min-height: calc(100vh - 64px);
  width: 60%;
  border-left: 1px solid #e0e0e0;
  /*background: #f6f6f6;*/
  padding: 32px 60px 32px 32px;
  margin: -32px -60px -32px 0;
}

.event_right-side_header {
  display: flex;
  flex-direction: column;
}

.event_right-side_header_title {
  font-size: 20px;
  font-weight: bold;
  line-height: 28px;
}

.event_right-side_header_description {
  display: flex;
  flex-wrap: wrap;
  color: #cdcdcd;
  font-size: 15px;
}

.event_right-side_header_description span:last-child {
  margin-left: auto;
}

.event_right-side_table {
  margin-top: 24px;
  width: 100%;
}

.event_right-side_table table {
  border-collapse: collapse;
  width: 100%;
}

.event_right-side_table table thead tr {
  height: 30px;
}

.event_right-side_table table thead th {
  padding: 0;
  font-size: 12px;
  height: 17px;
  line-height: 17px;
  text-align: left;
  text-transform: uppercase;
}

.event_right-side_table table tbody tr {
  border-top: 1px solid #EAEAEA;
  height: 50px;
}

.event_right-side_table table tbody tr:first-child {
  border-color: #ACACAC;
}

.event_right-side_table table tbody tr td {
  height: 52px;
  vertical-align: middle;
  font-size: 15px;
  align-items: center;
}

.event_right-side_table table tbody tr td span {
  margin-left: 10px;
  line-height: 32px;
}

.event_right-side_table table tbody tr td .dealer_user_avatar {
  display: block;
  float: left;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.event_right-side_table_bids-list {
  display: flex;
}

.event_right-side_table_bids-list div {
  display: flex;
  flex-direction: column;
  height: 44px;
  border-radius: 4px;
  background-color: #edf8fd;
  padding: 1px 14px;
  align-items: center;
  margin-right: 4px;
}

.event_right-side_table_bids-list div span {
  line-height: 20px !important;
  font-size: 15px !important;
  margin: 0 !important;
}

.event_right-side_table_bids-list div span:first-child {
  font-weight: 600;
}

.event_right-side_table_bids-list div span:last-child {
  color: #999999;
}

.event_left-side_card_img,
.event_item__pic {
  width: 100%;
  height: 160px;
  background-size: 100% !important;
  background-repeat: no-repeat;
  background-position: center;
}

.event_detail_table_avatar {
  width: 32px;
  height: 32px;
  display: inline-block;
  vertical-align: middle;
  border-radius: 4px;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
}

.t_from_img {
  display: inline-block;
  vertical-align: middle;
}

.t_from span {
  vertical-align: middle;
}

.event_filters {
  display: flex;
  margin: 0 -4px 6px;
}

.event_filters__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 18px;
}

.event_filters__item {
  width: 25%;
  border-radius: 6px;
  background-color: rgba(0, 0, 0, 0.04);
  margin: 0 4px;
}

.event_filters__item_select {
  height: 38px;
  display: flex;
  margin: 0;
  padding: 0 12px;
  background: #f5f5f5;
  border-radius: 6px;
  font-size: 15px;
}

.event_filters__item_select md-select-menu {
  margin-top: 45px;
}

.event_filters__active {
  display: flex;
  flex-wrap: wrap;
}

.event_filters__active_wrapper {
  display: flex;
  align-items: center;
  margin-right: 36px;
  height: 40px;
}

.event_filters__active_title {
  line-height: 20px;
  font-size: 15px;
  color: #808080;
  margin-right: 12px;
}

.event_filters__active_item {
  display: flex;
  align-items: center;
  background: #007fed;
  height: 28px;
  border-radius: 6px;
  padding: 0 8px;
  margin: 0 4px;
  color: #ffffff;
  cursor: pointer;
}

.event_filters__active_item__title {
  line-height: 28px;
  font-size: 15px;
  margin-right: 8px;
  white-space: nowrap;
}

.event_filters__active_item__close {
  opacity: 0.4;
}

.event_filters__active_item:hover .event_filters__active_item__close {
  opacity: 0.8;
}

.event_filters__close_all {
  position: absolute;
  cursor: pointer;
  width: 38px;
  height: 38px;
  right: -46px;
  border-radius: 6px;
  font-size: 28px;
  font-weight: 100;
  background-color: rgba(0, 0, 0, 0.04);
  display: flex;
  justify-content: center;
}

.event_filters__close_all span {
  opacity: 0.6;
}

.event_filter_select_multiple {
  top: 82px !important;
  margin-left: 26px;
  background-color: #f5f5f5;
}

.event_filter_select_multiple md-option {
  background-color: #f5f5f5;
