.add_new {
  width: 38px;
  height: 38px;
  border-radius: 6px;
  background-color: #007fed;
  position: relative;
  cursor: pointer;
  align-self: flex-end;
}

.add_new::before {
  content: '';
  display: block;
  width: 16px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  top: calc(50% - 1px);
  left: calc(50% - 8px);
}

.add_new::after {
  content: '';
  display: block;
  width: 16px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  transform: rotate(90deg);
  top: 50%;
  left: calc(50% - 8px);
}

