.sale {
  &_container {
    margin-bottom: 28px;
  }

  &_title {
    width: 100%;
    height: 33px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 17px;

    div:first-child {
      text-transform: capitalize;
      margin-right: 7px;
    }

    > div {
      display: flex;

    }
    &__text {
      opacity: 0.98;
      font-size: 24px;
      font-weight: bold;
      color: #000000;
      margin-right: 20px;
      &_disabled {
        color: rgba(0, 0, 0, 0.2);
      }
    }
    &__btn {
      width: 127px;
      line-height: 32px;
      border-radius: 6px;
      background-color: #007fed;
      color: #ffffff;
      text-align: center;
      cursor: pointer;
    }
    &__link {
      width: 48px;
      opacity: 0.98;
      font-size: 15px;
      font-weight: 600;
      line-height: 14px;
      color: #007fed;
      align-self: flex-end;
    }
  }

  &_row {
    display: flex;
    flex-wrap: wrap;
    margin: 16px -16px 0 -6px;
  }

  &_card_wrapper {
    width: 33%;
    min-width: 256px;
    box-sizing: border-box;
    padding: 0 6px;
    margin-bottom: 12px;
  }

  &_card {
    border-radius: 4px;
    padding: 12px 16px;
    //margin: 0 6px;

    &__yellow {
      background-color: #fff9e8;
    }
    &__grey {
      background-color: #f6f6f6;
    }
    &__title {
      font-size: 15px;
      font-weight: 600;

    }
    &__price {
      font-size: 12px;
      font-weight: 400;
    }
    &__options {
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin: 0 -2px;

      &_column {
        display: flex;
        flex-direction: column;
        margin-top: 8px;
        padding: 0 2px;

        span {
          white-space: nowrap;
        }
        span:first-child {
          font-size: 12px;
          color: #949494;
          line-height: 17px;
        }
        span:last-child {
          font-size: 15px;
          line-height: 20px;
        }
      }

    }

  }

  /*active sales table*/
  &_table {
    width: 100%;
    border-spacing: 0;
    &_head {
      width: 100%;
      &_td {
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        text-align: left;
        height: 33px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
      }
      &_td:last-child {
        text-align: right;
      }

    }
    &_body {
      &__yellow {
        tr {
          td {
            background-color: #fff9e8;
          }
        }

      }
      &__grey {
        tr {
          td {
            background-color: #f6f6f6;
          }
        }

      }

    }
    &_row {
      height: 77px;
      font-size: 15px;
      td {
        border-bottom: 1px solid rgba(0, 0, 0, 0.3);
        //background-color: #fff9e8;

      }
      td:last-child {
        text-align: right;
        padding-right: 10px;
      }
      &__title {
        padding-left: 10px;
        span:first-child {
          font-weight: 600;
        }
        span:last-child {
          color: rgba(0, 0, 0, 0.6);
        }
      }
      &__data {
        color: rgba(0, 0, 0, 0.6);
        &_bold {
          font-weight: 700;
          color: #000;
          &_green {
            color: #00c811;
          }
        }

      }
    }
  }
  /*create sale*/
}

.modal {
  &_wrapper {
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: flex-end;

  }
  &_content_wrapper {
    width: 500px;
    height: 100%;
    background-color: #fff;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.02), 0 4px 4px 0 rgba(0, 0, 0, 0.02), 0 8px 8px 0 rgba(0, 0, 0, 0.02), 0 16px 16px 0 rgba(0, 0, 0, 0.02);
    border: solid 1px rgba(0, 0, 0, 0.04);
    box-sizing: border-box;

  }
  &_content {
    position: relative;
    width: 100%;
    overflow: hidden;
    &__title {
      width: 100%;
      height: 33px;
      font-size: 24px;
      color: #000000;
      font-weight: 700;
      line-height: 33px;
      text-align: left;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;

      .close {
        width: 14px;
        height: 14px;
        background: url("/images/close.png") no-repeat center;
        background-size: 100%;
        cursor: pointer;
      }
    }
    &__sale {
      width: 100%;
      &_title {
        font-size: 15px;
        font-weight: 600;
        color: rgba(0, 0, 0, 0.34);
        text-transform: uppercase;
        margin-bottom: 12px;
        &_select {
          text-transform: capitalize;
            md-select-menu{
                margin-left: -10px !important;
            }
        }
      }
      &_select {
        width: 100%;
        height: 38px;
        background-color: rgba(0, 0, 0, 0.04);
        border-radius: 6px;
        margin-bottom: 16px;
        margin-top: 4px;
      }

      .attributs_wrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
        .attributs_item {
          font-size: 15px;
          color: rgba(0, 0, 0, 0.5);
          margin-bottom: 20px;
          span {
            display: block;
            margin-bottom: 4px;
          }

          input {
            border: none;
            outline: none;
            border-bottom: 1px solid #f2f2f2;
            padding-bottom: 20px;
            font-size: 18px;
            color: #000;
          }
            input::placeholder{
                color: #949494;
            }

            input:focus::placeholder{
                color: transparent;
            }

        }
        .attributs_item:first-child {
          width: 45%;
        }
        .attributs_item:last-child {
          width: 55%;
        }
        .attributs_item__border {
          width: 132px !important;
          overflow: hidden;
          input, div {
            width: 100%;
            background-color: rgba(0, 0, 0, 0.04);
            box-sizing: border-box;
            padding: 8px 0 10px 12px;
            border-radius: 6px;
          }
            > div{
                height: 42px;
                color: black;
                font-size: 18px;
            }
        }
      }
    }
  }
  &_foter {
    width: 500px;
    height: 70px;
    border-top: 1px solid #f2f2f2;
    position: fixed;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &__buttons {
      display: flex;
      height: 38px;
      margin-right: 40px;
      .button {
        &_blue {
          width: 148px;
          height: 38px;
          line-height: 38px;
          border-radius: 6px;
          background-color: #007fed;
          color: #fff;
          text-align: center;
          font-size: 15px;
          font-weight: 600;
          margin-left: 20px;
          cursor: pointer;
        }
        &_grey {
          width: auto;
          padding: 0 27px;
          background-color: rgba(0, 0, 0, 0.04);
          color: #000000;
        }
      }

      .button_text {
        height: 38px;
        line-height: 38px;
        cursor: pointer;
      }
    }
  }
}

.sale-manage__attributes {
  display: flex;

  &__title {
    font-size: 12px;
    line-height: 17px;
    color: #949494;
    margin-left: 12px;
    margin-bottom: 4px;
  }

  &__value {
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    background: #fff9e8;
    font-size: 15px;
    line-height: 40px;
    padding: 0 0 0 12px;
    border: none;
    border-left: 1px solid white;
    margin:0;
  }

  &__markup {
    display: flex;
    flex-direction: column;
    width: 25%;

    & .sale-manage__attributes__title{
      margin-left: 0;
    }

    & .sale-manage__attributes__value{
      font-weight: 600;
      border-left:none;
      border-radius: 4px 0 0 4px;
    }
  }

  &__share {
    display: flex;
    flex-direction: column;
    width: 25%;
  }

  &__amount {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  &__edit {
    display: flex;
    flex-direction: column;
    width: 40px;
    border-left: 1px solid white;
    justify-content: flex-end;
  }

  &__btn {
    padding: 0;
    margin: 0;
    width: 40px;
    height: 40px;
    border: none;
    background-color: #fff9e8;
    background-image: url('/images/edit.svg');
    background-repeat: no-repeat;
    background-size: 24px;
    background-position: center;
  }
}

.md-select-menu-container.md-active md-select-menu{
    margin-left: -10px;
}





